@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Helvetica', 'Open Sans', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{font-size:62.5%;box-sizing:border-box}body{font:1.37rem 'Poppins', sans-serif;height:100vh;margin:0}p::selection{background-color:#ff9100}h3::selection{background-color:#ff9100}h2::selection{background-color:#ff9100}h1::selection{background-color:#ff9100}.App{padding:5rem}.App .header{position:fixed;transform:translateY(-5rem);background-color:white;width:100%}.App .main{height:100vh;display:flex;align-items:flex-end}.App .main h1{font-size:12rem}@media (max-width: 600px){.App .main h1{font-size:5rem}}.App .section{margin-top:10.5rem;display:flex;flex-wrap:wrap}.App .section .section-title{flex-grow:1;max-width:35rem}.App .section .section-title h2{margin:0}.App .section .section-items{flex-grow:3}.App .section .section-items .item{display:flex;flex-wrap:wrap;justify-content:space-between;border-bottom:solid 0.3px #afafaf;padding:3.5rem;color:#161616}.App .section .section-items .item a{text-decoration:underline;color:black}.App .section .section-items .item a h3{font-weight:bold;color:black}.App .section .section-items .item .content{max-width:60rem;line-height:3.3rem}@media (max-width: 600px){.App .section .section-items .item .content{max-width:25rem}}.footer .text-section{display:flex;justify-content:center;align-items:center;font-size:3.2rem;margin:2.6rem}img{height:40rem}@media (max-width: 600px){img{width:100%;height:auto}}

